export default {
	onLoad() {
		this.getPetList();
	},
	onShow(){
		this.getPetList();
	},
	data() {
		return {
			petList: [],
			api: this.$API
		}
	},
	methods: {
		// 进入宠物百科
		enterEncyclopedia() {
			uni.navigateTo({
				url: './encyclopedia/encyclopedia'
			})
		},
		// 获取用户宠物列表
		getPetList() {
			this.$http.get('/petbnb/getPetList')
				.then(res => {
					if (res.code === 1) {
						this.petList = res.data.list;
					}
				})
				.catch(err => {

				})
		},
		// 进入添加宠物信息页面
		addPet() {
			uni.navigateTo({
				url: './pet/addPet'
			})
		},
		// 进入记账本页面
		enterAccount(){
			uni.navigateTo({
				url: './account/account'
			})
		},
		// 进入文章页面
		enterArticle(){
			uni.navigateTo({
				url: './article/article'
			})
		},
		// 进入同城服务
		enterService(){
			uni.navigateTo({
				url: './service/service'
			})
		}
	}
}
